/**
 * 
 */
import React from 'react';
// 
import { Link } from 'react-router';
// Menu  Icon 
import { Menu, Icon } from 'antd';
import styles from '../styles/home-layout.less';

// 
const SubMenu = Menu.SubMenu;
 
class HomeLayout extends React.Component {
  render () {
    const {children} = this.props;
    return (
      <div>
        <header className={styles.header}>
          <Link to="/">ReactManager</Link>
        </header>
 
        <main className={styles.main}>
          <div className={styles.menu}>
            <Menu mode="inline" theme="dark" style={{width: '240'}}>
              <SubMenu key="user" title={<span><Icon type="user"/><span></span></span>}>
                <Menu.Item key="user-list">
                  <Link to="/user/list"></Link>
                </Menu.Item>
                <Menu.Item key="user-add">
                  <Link to="/user/add"></Link>
                </Menu.Item>
              </SubMenu>
 
              <SubMenu key="book" title={<span><Icon type="book"/><span></span></span>}>
                <Menu.Item key="book-list">
                  <Link to="/book/list"></Link>
                </Menu.Item>
                <Menu.Item key="book-add">
                  <Link to="/book/add"></Link>
                </Menu.Item>
              </SubMenu>
            </Menu>
          </div>
 
          <div className={styles.content}>
            {children}
          </div>
        </main>
      </div>
    );
  }
}
 
export default HomeLayout;


