/**
 * 
 */
import React from 'react';
//  prop-types
import PropTypes from 'prop-types';
//  fetch
import { get, del } from '../utils/request'; 

class BookList extends React.Component {
  // 
  constructor(props) {
    super(props);
    // 
    this.state = {
      bookList: []
    };
  }

  /**
   * 
   * componentWillMount
   * 
   */
  componentWillMount(){
    // 
    get('http://localhost:8000/book')
      .then((res) => {
        /**
         * 
         * 
         */
        this.setState({
          bookList: res
        });
      });
  }

  /**
   * 
   */
  handleEdit(book){
    // 
    this.context.router.push('/book/edit/' + book.id);
  }

  /**
   * 
   */
  handleDel(book){
    // 
    const confirmed = window.confirm(` ${book.name} ?`);
    // 
    if(confirmed){
      // 
      del('http://localhost:8000/book/' + book.id, {
      })
      .then(res => {
        /**
         * 
         * array.filter
         * Array
         */
        this.setState({
          bookList: this.state.bookList.filter(item => item.id !== book.id)
        });
        alert('');
      })
      .catch(err => {
        console.log(err);
        alert('');
      });
    }
  }

  render() {
    // 
    const { bookList } = this.state;

    return (
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>

        <tbody>
          {
            bookList.map((book) => {
              return (
                <tr key={book.id}>
                  <td>{book.id}</td>
                  <td>{book.name}</td>
                  <td>{book.price}</td>
                  <td>
                    <a onClick={() => this.handleEdit(book)}></a>
                    &nbsp;
                    <a onClick={() => this.handleDel(book)}></a>
                  </td>
                </tr>
              );
            })
          }
        </tbody>
      </table>
    );
  }
}

/**
 * this.context.xxxcontextTypesPropTypes
 */
BookList.contextTypes = {
  router: PropTypes.object.isRequired
};

export default BookList;


