/**
 * 
 */
import React from 'react';
import FormItem from '../components/FormItem'; //  ./FormItem
//  formProvider
import formProvider from '../utils/formProvider';
//  prop-types
import PropTypes from 'prop-types';
//  fetch
import request from '../utils/request'; 

class UserEditor extends React.Component {
  // 
  handleSubmit(e){
    // submit
    e.preventDefault();
    // 
    const { form: { name, age, gender }, formValid, editTarget} = this.props; // 
    // 
    if(!formValid){
      alert('');
      return;
    }

    // 
    let editType = '';
    let apiUrl = 'http://localhost:8000/user';
    let method = 'post';
    // 
    if(editTarget){
      editType = '';
      apiUrl += '/' + editTarget.id;
      method = 'put';
    }

    // 
    request(method,apiUrl, {
      name: name.value,
      age: age.value,
      gender: gender.value
    })
    // 
    .then((res) => {
      // ,jsonid
      // res.id
      if(res.id){
        alert(editType + '!');
        this.context.router.push('/user/list'); // 
        return;
      }else{
        alert(editType + '!');
      }
    })
    // 
    .catch((err) => console.error(err));
  }

  // --
  componentWillMount(){
    const {editTarget, setFormValues} = this.props;
    if(editTarget){
      setFormValues(editTarget);
    }
  }
  
  render() {
    // 
    const {form: {name, age, gender}, onFormChange} = this.props;
    return (
      <form onSubmit={(e) => this.handleSubmit(e)}>
        <FormItem label=":" valid={name.valid} error={name.error}>
          <input
            type="text"
            value={name.value}
            onChange={(e) => onFormChange('name', e.target.value)}/>
        </FormItem>

        <FormItem label=":" valid={age.valid} error={age.error}>
          <input
            type="number"
            value={age.value || ''}
            onChange={(e) => onFormChange('age', e.target.value)}/>
        </FormItem>

        <FormItem label=":" valid={gender.valid} error={gender.error}>
          <select
            value={gender.value}
            onChange={(e) => onFormChange('gender', e.target.value)}>
            <option value=""></option>
            <option value="male"></option>
            <option value="female"></option>
          </select>
        </FormItem>
        <br />
        <input type="submit" value="" />
      </form>
    );
  }
}

// UserEditorroutercontextTypes
// this.context.routerReact Router
UserEditor.contextTypes = {
  router: PropTypes.object.isRequired
};

// 
UserEditor = formProvider({ // field 
  // 
  name: {
    defaultValue: '',
    rules: [
      {
        pattern: function (value) {
          return value.length > 0;
        },
        error: ''
      },
      {
        pattern: /^.{1,4}$/,
        error: '4'
      }
    ]
  },
  // 
  age: {
    defaultValue: 0,
    rules: [
      {
        pattern: function(value){
          return value >= 1 && value <= 100;
        },
        error: '1~100'
      }
    ]
  },
  // 
  gender: {
    defaultValue: '',
    rules: [
      {
        pattern: function(value) {
          return !!value;
        },
        error: ''
      }
    ]
  }
})(UserEditor);

export default UserEditor;


