import React from 'react';
// 
import styles from '../styles/home-page.less';

class Home extends React.Component {
  // 
  constructor(props) {
    super(props);
    // 
    this.state = {};
  }

  render() {
    return (
      <div className={styles.welcome}>
        Welcome
      </div>
    );
  }
}

export default Home;


